import React from 'react'
import Layout from '../components/layout'
import Result from '../components/result'
import fetch from 'isomorphic-unfetch'

import { Input, Row, Col, Typography, message } from 'antd';

const { Text, Title } = Typography;
const { Search } = Input;

const API_HOST = 'http://localhost:5000'

class Index extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			result_expand: [],
			result_no_expand: []
		}
	}

	doSearch = (query) => {
		fetch(`${API_HOST}/search?mock=true`, {
			method: 'POST',
			headers: {
				'Accept': 'application/json',
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({ query })
		})
			.then(res => res.json())
			.then(res => {
				console.log(res)
				const { result_expand, result_no_expand } = res.result
				this.setState({ result_expand, result_no_expand }, () => {
					console.log( this.state.result_expand )
				})
			})
			.catch(err => {
				message.error(`Error ${err}`)
			})
	}

	render () {
		return (
			<Layout>

				<Row>
					<Col span={12} offset={6}>
						<Title level={2}>
							Information Retrivial Demo
						</Title>
						<Text>Search Engine using LSI, word2vec for query expansion ...</Text>

						<div style={{ marginTop: 20 }}>
							<Search
								placeholder="input search text"
								onSearch={value => this.doSearch(value)}
								enterButton
								size="large"
							/>
							<Text type="secondary">
								e.g. <a>xxx</a>, <a>yyy</a>, ...
							</Text>
						</div>
					</Col>
				</Row>

				<Row gutter={[16, 16]}>
					<Col span={12}>
						<Result results={this.state.result_no_expand} title="Result without query expansion" />
					</Col>

					<Col span={12}>
						<Result results={this.state.result_expand} title="Result with query expansion" />
					</Col>
				</Row>


			</Layout>
		)
	}
}

export default Index;


